import React from 'react'
import { Row, Col } from 'antd'
import Footer from '@/components/app-footer'
import { useBreadcrumb } from '@/hooks/useApp'
import useAuth from '@/hooks/useAuth'
import history from '@/utils/history'
import { REPORT_LIST, RADIOL_REPORT_LIST } from '@/constants/routes'
import { Divisions } from '@/constants/enums'
import { Block, HomePage } from './components'

export default function Home() {
  useBreadcrumb([{ name: '首頁' }])

  const { user, setUser } = useAuth()

  const handleTo = (pathname: string) => () => {
    if (user.isAdmin) {
      setUser((prev: any) => ({ ...prev, viewDivision: true }))
      return
    }
    history.push(pathname)
  }

  const handleDivisionSelect = (division: Divisions) => () => {
    setUser((prev: any) => ({
      ...prev,
      division,
      viewDivision: true,
    }))
  }

  const showLink =
    user.division === 'CLINICIAN' || (user.isAdmin && !user.viewDivision)

  return (
    <>
      {!showLink && <HomePage />}
      {showLink && (
        <div style={{ height: 'calc(100vh - 204px)' }}>
          <Row>
            <Col span={24} className="text-center">
              <div css={{ marginTop: 200 }}>
                <Block
                  onClick={
                    user.isAdmin
                      ? handleDivisionSelect(Divisions.RAD)
                      : handleTo(RADIOL_REPORT_LIST)
                  }
                  style={{ marginRight: 100 }}>
                  放射科
                </Block>
                <Block
                  onClick={
                    user.isAdmin
                      ? handleDivisionSelect(Divisions.PATH)
                      : handleTo(REPORT_LIST)
                  }>
                  病理科
                </Block>
              </div>
            </Col>
          </Row>
        </div>
      )}
      <Footer />
    </>
  )
}
